.GamePage {
    display: flex;
    justify-content: center;
}

.GameContainer {
    width: 48vh;
    display: flex;
    justify-content: center;
    background-color: turquoise;
    padding: 1vh 0;
}

.GameBoard {
    background-color: black;
    height: 92vh;
    min-width: 46vh;
    display: flex;
    flex-wrap: wrap-reverse;
}

.BoardSquare {
    border: 1px solid transparent;
    width: 4.6vh;
    height: 4.6vh;
}

.VisibleSquare {
    border: 1px solid gray;
}

/* .GameBoard button {
    position: absolute;
    top: 48%;
    background-color: #e06540;
    border: 2px solid black;
} */

.GameBoard button p {
    font-size: calc(0.8rem + 0.4vw);
    margin: 0;
}

#startButton {
    width: 6%;
    height: 6%;
    left: 47%;
}

#startButton:hover {
    cursor: pointer;
    color: turquoise;
    border: 2px solid turquoise;
}

#pauseButton {
    width: 7%;
    height: 6%;
    left: 46.5%;
}

.NextList, .InfoBox {
    width: 22vh;
    margin: 0 1vh;
    position: relative;
    border: 1vh solid turquoise;
    top: -1vh;
    background-color: turquoise;
}

.NextList {
    height: 82vh;
    border-radius: 0 1rem 0 0;
    right: 1vh;
    min-width: 22vh;
}

.NextList h2, .InfoBox h2 {
    height: 5vh;
    margin: 0;
    padding: calc(2.5vh - 0.8625rem);
    color: black;
    border-radius: 0 1rem 0 0;
    font-size: calc(1rem + 0.5vw);
}

#nextTetrominoes {
    height: calc(100% - 5vh);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
}

#nextTetrominoes > div {
    height: calc(87vh / (21 / 2));
    width: calc(87vh / (21 / 4));
    margin: calc(87vh / 42) 0;
    display: flex;
    flex-wrap: wrap;
}

#nextTetrominoes > div > div, #holdContainer > div > div {
    width: 25%;
    height: 50%;
}

.GamePage .MinoDiv {
    border: 1px solid gray;
}

.InfoBox {
    height: 48vh;
    border-radius: 1rem 0 0 0;
    left: 1vh;
    min-width: 22vh;
}

.InfoBox > div {
    background-color: black;
    height: 6vh;
    margin-bottom: 2vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.InfoBox p {
    color: white;
    font-size: calc(1rem + 0.5vw);
}

.InfoBox #holdContainer {
    height: 15vh;
}

#holdContainer > div {
    width: 17vh;
    height: 8.5vh;
    display: flex;
    flex-wrap: wrap;
}

.InfoBox .ToolBar {
    /* border: 1px solid white; */
    position: absolute;
    width: 21vh;
    height: 6vh;
    bottom: -9vh;
    right: 0;
    background: none;
    color: gray;
    font-size: calc(1rem + 0.5vw);
    justify-content: space-evenly;
}

.ToolBar > svg {
    cursor: pointer;
}

.ToolBar a {
    display: flex;
    color: gray;
}

#gameOverContainer {
    position: absolute;
    height: 25%;
    width: 48vh;
    background-color: black;
    border: 1vh solid #e06540;
    top: 40%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* text-align: center; */
}

#gameOverContainer h2 {
    color: turquoise;
    word-spacing: 500%;
    /* height: 10%; */
    width: 100%;
}

#gameOverContainer button {
    height: 30%;
    width: 40%;
    background-color: turquoise;
    position: static;
}

#gameOverContainer button:hover {
    /* border: 2px solid #e06540; */
    background-color: #e06540;
    cursor: pointer;
} 

#gameOverContainer button p {
    font-size: calc(0.6rem + 0.3vw);
}

#gameOverContainer button:hover p {
    /* color: #e06540; */
    color: turquoise;
}

#gameOverContainer a {
    width: 100%;
    color: turquoise;
}








@keyframes lineClear {
    to {background-color: white;}
}

