}

.TetrominoJ .MinoDiv {
    background-color: blue;
}

.TetrominoL .MinoDiv {
    background-color: orange;
}

.TetrominoO .MinoDiv {
    background-color: yellow;
}

.TetrominoS .MinoDiv {
    background-color: green;
}

.TetrominoT .MinoDiv {
    background-color: purple;
}

.TetrominoZ .MinoDiv {
    background-color: red;
}

.Value1 {
    background-color: cyan;
}

.Value2 {
    background-color: blue;
}

.Value3 {
    background-color: orange;
}

.Value4 {
    background-color: yellow;
}

.Value5 {
    background-color: green;
}

.Value6 {
    background-color: purple;
}

.Value7 {
